import React from 'react'

import Sidebar from 'internetShop/components/Sidebar'

const Layout = ({ children }) => {
  return (
    <div className='layout'>
      <div className="layout__sidebar">
        <Sidebar />
      </div>
      <div className="layout__content">
        {children}
      </div>
    </div>
  )
}

export default Layout