import React from 'react'

const Search = ({ onSearchTask }) => {
  return (
    <div className='task-search mr-3 w-100'>
      <input
        type="task-search"
        placeholder='type to search'
        className='form-control w-100 p-2'
        onChange={onSearchTask}
      />
    </div>
  )
}

export default Search