import { combineReducers } from 'redux'

import phones from 'reducers/internetShop/phones'
import phonesPage from 'reducers/internetShop/phonesPage'
import phonePage from 'reducers/internetShop/phonePage'
import basket from 'reducers/internetShop/basket'
import categories from 'reducers/internetShop/categories'

export default combineReducers({
  phones,
  phonesPage,
  phonePage,
  basket,
  categories
})